import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import howto_search from './howto_search.png';

function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: 'absolute',
    width: 800,
    backgroundColor: theme.palette.background.paper,
    border: '1px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function SimpleModal() {
  const classes = useStyles();
  // getModalStyle is not a pure function, we roll the style only on the first render
  const [modalStyle] = React.useState(getModalStyle);
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const body = (
    <div style={modalStyle} className={classes.paper}>
      <ol>
          <li>Enter gene, function, or OpenProt accession number in the search bar.</li>
          <li>A list of nodes will show. eg.:
              <p><img src={howto_search} height={200}/></p>
          </li>
          <li>
              Click on the node button to see the second neighborhood (direct neighbors and neighbors of neighbors), or on the cluster button to see the cluster associated with that node.
              Clusters are the result of the <a href="https://github.com/GuyAllard/markov_clustering">markov clustering algorithm</a> applied to the whole network. 
              If present, significant enrichment of Gene Ontology terms for clusters appear below the network. Enrichment was computed against the background of the whole network and p-value below 0.05 was deemed significant.
          </li>
          <li>For more detail on a particular alternative protein, click on the node in the network and a link to the relevant OpenProt page will appear on top of the network.</li>
      </ol>

    </div>
  );

  return (
    <div>
      <button type="button" onClick={handleOpen}>
        Help
      </button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {body}
      </Modal>
    </div>
  );
}